* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}
/*--------------------*/

body {
  font-family: 'Lato', sans-serif;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  margin: 50px 20px;
  max-width: 1000px;
  width: 100%;

  background: #fff;
  border-radius: 25px;
  box-shadow: 0px 0px 100px 0px rgba(66, 66, 66, 0.24);

  padding: 50px 50px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.navbar li {
  display: inline-block;
}

h1 {
  font-size: 42px;
  color: #424242;
  font-weight: 900;
  margin-bottom: 50px;
}

.todo__item {
  width: 100%;
  margin: 50px 20px;
  padding: 25px 25px;

  /* border: 1px solid #4242423d; */
  /* border-radius: 100px;
    background: #4242423d; */

  box-shadow: 0px 0px 25px 0px rgba(66, 66, 66, 0.2);
  border-radius: 100px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.todo__item__text {
  cursor: pointer;
}

.strike {
  text-decoration: line-through;
  opacity: 0.5;
}

.todo__item__text__header {
  color: #424242;
  font-size: 18px;
  font-weight: 700;
  max-width: 470px;
}

.todo__item__text__parag {
  color: #424242;
  font-size: 16px;
  font-weight: 400;
  margin-top: 10px;
  opacity: 0.8;
}

.todo__item__buttons {
  cursor: pointer;
}

button {
  padding: 5px 20px;
  background: #fff;
  border: 1px solid #424242;
  border-radius: 100px;

  font-family: 'Lato', sans-serif;
  color: #424242;
  font-size: 14px;
  font-weight: 700;

  transition: 0.3s;
}

button:hover {
  background: #424242;
  color: #fff;
}

button:not(:last-child) {
  margin-right: 15px;
}

form {
  display: flex;
  flex-direction: row;
}

input {
  font-family: 'Lato', sans-serif;
  color: #424242;
  font-weight: 700;

  padding: 10px 15px;
  width: 400px;
  border: 1px solid #424242;
  border-radius: 100px;
  margin-right: 25px;
}

.error {
  box-shadow: 0px 0px 20px 0px rgba(247, 0, 0, 0.2);
  border: 1px solid red;
}

.bx-trash {
  font-size: 32px;
  color: #424242;
  opacity: 0.5;
  transition: 0.3s;
}

.bx-trash:hover {
  opacity: 1;
}

@media screen and (max-width: 1024px) {
  input {
    width: 300px;
  }
}

@media screen and (max-width: 896px) {
  form {
    flex-direction: column;
  }

  input {
    margin-right: 0;
    margin-bottom: 15px;
  }

  button {
    height: 35px;
  }
}

@media screen and (max-width: 480px) {
  input {
    width: 200px;
  }
}
